$primary: #0052e9;
$secondary: #ffab00;
$black10: rgba(0, 0, 0, 0.1);

.color-primary {
  color: $primary;
}

.color-secondary {
  color: $secondary;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', 'Roboto', sans-serif;
  color: #3a3f52;
  padding-top: 83px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

strong {
  font-weight: 600;
}

a {
  &.text-link {
    color: rgba(0, 0, 0, 0.83);
    text-decoration: underline;
    margin: 0 2px 0 4px;
  }

  &:hover {
    text-decoration: none;
  }
}

p,
li {
  font-weight: 300;
  letter-spacing: .05em;
  color: rgba(0, 0, 0, 0.83);
}

section {
  margin: 100px 0px;

  // &:nth-child(even) {
  //   background: #FAFAFA;
  // }
}

hr{
  margin: 32px 0;
}

.crop {
  width: 100%;
  margin-bottom: 20px;
  overflow: hidden;
  text-align: center;
  display: inline-block;
  position: relative;
  &:after {
    padding-top: 56.25%;
    display: block;
    content: '';
  }
  img {
    position: absolute;
    width: 100%;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
  }
}


/* ==============================================
     2. Navbar 
  ===============================================*/

.navbar {
  background-color: white;
  //box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.09);
  padding: 20px;

  .navbar-brand {
    padding-left: 0;
  }

  .logo-img {
    max-height: 40px;
    border: 3px solid white;
    border-radius: 50%;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .07);
  }

  .logo-text {
    display: inline-block;
    letter-spacing: 4px;
    margin: 0 0 0 10px;
    font-size: 1.2em;
    font-weight: 400;
  }

  a {
    font-size: 15px;
    font-weight: 400;
    letter-spacing: .05em;
    padding: 10px 16px;
    display: block;
    color: black;

    &:hover {
      color: $primary;
      text-decoration: none;
    }
  }

  /* Large devices (desktops, 992px and up) */
  @media (min-width: 992px) {
    &.navbar-expand-lg .navbar-nav .nav-link {
      padding-left: 40px;
    }
  }

}



